class Utils {
  parseQueryString = (string) => {
    // Массив, который мы будем наполнять параметрами из Query String: query[key] => value
    var query = [];
    // Убираем "?" в начале
    string = string.slice(1);
    // Разбиваем на "param1=value1", "param2=value2", ..., "paramN=valueN"
    var queryParams = string.split("&");
    // Разбиваем каждый "paramN=valueN" на Key Value Pairs: paramN => valueN
    queryParams.forEach(function (queryParam) {
      let keyValuePair = queryParam.split("=");
      var key = keyValuePair[0];
      var value = keyValuePair[1];
      // Наполняем итоговый массив значениями параметров
      query[key] = value;
    });
    return query;
  };
}

export default new Utils();
