import React, { Component } from "react";

class StarRating extends Component {
  render() {
    // This is the JSX we'll be filling
    let starRating = [];
    var i;
    var rating = this.props.rating;
    var roundedRating = Math.round(rating);
    for (i = 0; i < 5; i++) {
      var checked = "";
      if (i < roundedRating) checked = "checked";
      starRating.push(
        <span
          key={i}
          className={"fa fa-star " + checked + " mr-1 h1"}
          title={"Рейтинг: " + Number(rating).toLocaleString("ru-RU")}
        ></span>
      );
    }

    return (
      <React.Fragment>
        <div className="star-rating">{starRating}</div>
        <h3>
          Рейтинг:{" "}
          {Number(rating).toLocaleString("ru-RU", { minimumFractionDigits: 1 })}
        </h3>
      </React.Fragment>
    );
  }
}

export default StarRating;
