import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

import Main from "./components/main.component";
import Business from "./components/business.component";
import Checker from "./components/checker.component";
import Quarantine from "./components/quarantine.component";
import LeaveFeedback from "./components/leave-feedback.component";

// Stateless functional component
function App() {
  return (
    <Router>
      <br />
      <div className="container">
        {/* <Route path="/" exact component={Business} /> */}
        <Route path={process.env.PUBLIC_URL + "/"} exact component={Main} />
        <Route
          path={process.env.PUBLIC_URL + "/business"}
          component={Business}
        />
        <Route path={process.env.PUBLIC_URL + "/checker"} component={Checker} />
        <Route
          path={process.env.PUBLIC_URL + "/quarantine"}
          component={Quarantine}
        />
        <Route
          path={process.env.PUBLIC_URL + "/leave-feedback"}
          component={LeaveFeedback}
        />
      </div>
    </Router>
  );
}

export default App;
