import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import bridge from "@vkontakte/vk-bridge";
import Utils from "./Utils";

class Main extends Component {
  constructor() {
    super();
    this.state = {
      redirect: false,
      group_id: null,
    };
  }

  componentDidMount() {
    console.log(sessionStorage);
    // return;
    // Get User AuthToken
    var user_access_token = sessionStorage.getItem("user_access_token");
    var app_id = sessionStorage.getItem("app_id");

    if (!user_access_token || !app_id) {
      // Get User AuthToken From Launch Params
      var queryParams = Utils.parseQueryString(window.location.search);
      console.log("queryParams:");
      console.log(queryParams);

      app_id = queryParams["vk_app_id"];
      this.getAuthToken(app_id);
    }

    var hashParams = Utils.parseQueryString(window.location.hash);
    console.log("hashParams:");
    console.log(hashParams);
    if (hashParams.group_id) {
      var group_id = hashParams.group_id;
      sessionStorage.setItem("group_id", group_id);
      this.setState({
        redirect: "/checker",
      });
    }
  }

  getAuthToken = (app_id) => {
    bridge
      .send("VKWebAppGetAuthToken", {
        app_id: Number(app_id),
        scope: "groups",
      })
      .then((data) => {
        var user_access_token = data.access_token;
        console.log("User Access Token: " + user_access_token);
        sessionStorage.setItem("user_access_token", user_access_token);
        sessionStorage.setItem("app_id", app_id);
      })
      .catch((error) => {});
  };

  render() {
    const { redirect } = this.state;
    if (redirect) {
      return <Redirect to={redirect} />;
    }
    return (
      <div>
        <h1>RateMe!</h1>
        <p>
          <Link to={process.env.PUBLIC_URL + "/leave-feedback"}>Link</Link>
        </p>
        {/* <h3>Система рейтинга и страхования ответственности бизнеса</h3> */}
        <p>
          Данный проект является некоммерческим и признан служить индикатором,
          позволяющим потребителю быть осведомленным о деловой репутации и
          степени удовлетворенности товарами и услугами конкретной группы
          ВКонтакте: кафе, магазина, Интернет-магазина, службы доставки,
          автосервиса, мастерской по ремонту и т.д.
        </p>
        <p>
          Сообщества, принимающие участие в проекте, дополнительно страхуют свою
          ответственность в целях обеспечения повышенного уровня доверия со
          стороны потребителей – минимизации вероятности отказа от покупки по
          причине риска, связанного с дистанционным взаимодействием.
        </p>
        <p>
          <Link
            to={process.env.PUBLIC_URL + "/checker"}
            className="btn btn-primary btn-lg btn-block"
            role="button"
          >
            Проверить группу
          </Link>
          <Link
            to={process.env.PUBLIC_URL + "/business"}
            className="btn btn-secondary btn-lg btn-block"
            role="button"
          >
            Для групп
          </Link>
        </p>
      </div>
    );
  }
}

export default Main;
