import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import bridge from "@vkontakte/vk-bridge";
import Utils from "./Utils";

import StarRating from "./star-rating.component";

class Checker extends Component {
  constructor(props) {
    super(props);

    this.getGroupRatingById = this.getGroupRatingById.bind(this);
    this.onChangeGroupUrl = this.onChangeGroupUrl.bind(this);
    this.onSubmitForm = this.onSubmitForm.bind(this);

    this.state = {
      group_url: "",
      result: null,
      error: null,
      loading: false,
      debug: null,
      group: null,
    };
  }

  componentDidMount() {
    // Get Hash Query String after #
    // E.g.: #group_id=34801287
    var hashParams = Utils.parseQueryString(window.location.hash);
    console.log("hashParams:");
    console.log(hashParams);

    console.log("sessionStorage:");
    console.log(sessionStorage);

    // Проверить, есть ли "group_id" в Session Storage.
    // Если есть, это означает, что в checker.component мы были
    // перенаправлены из Main Component, а "group_id" была записана
    // в Session Storage.
    var group_id = sessionStorage.getItem("group_id");
    if (group_id) {
      this.setState({
        group_url: "vk.com/club" + group_id,
      });
      sessionStorage.removeItem("group_id");
    }
  } // Closes componentDidMount

  getGroupRatingById(group_id) {
    return new Promise((resolve, reject) => {
      // Use HTTPS!
      // The ngrok Backend URL string is for local Development
      // var backend_url = "https://adc3deeada67.ngrok.io";
      // Heroku Backend
      var backend_url = "https://test-mern-app-2020-06-04.herokuapp.com";
      backend_url += "/";
      axios
        // We are using ngrok because VK does not retrieve
        // data from "localhost" in the iOS App
        // .get("http://localhost:5000/groups/" + group_id)
        .get(backend_url + "groups/" + group_id)
        .then(function (response) {
          // Handle Success
          resolve(response);
        })
        .catch(function (error) {
          // Handle Error
          reject(error);
        });
    });
  } // Closes getGroupStatusById

  onChangeGroupUrl(e) {
    this.setState({ group_url: e.target.value });
  }

  onSubmitForm(e) {
    e.preventDefault();

    this.setState({
      loading: true,
      error: null,
      result: null,
    });

    // Берем из Session Storage пользовательский ключ доступа
    // для дальнейшего запроса к API ВКонтакте: "getGroupById"
    const user_access_token = sessionStorage.getItem("user_access_token");

    var group_url = this.state.group_url;
    if (!group_url) {
      this.setState({
        loading: false,
        error: (
          <div className="alert alert-danger" role="alert">
            Пожалуйста, введите ссылку на группу.
          </div>
        ),
        result: null,
      });
      return;
    }

    // Remove:
    // - "http://" or "https://"
    // - "vk.com/"
    // - "club" or "public" or "event"
    var pattern = /^(https?:\/\/)?vk.com\/(club|public|event)?/iu;
    var group_id = group_url.replace(pattern, "");

    // VK API getGroupById
    bridge
      .send("VKWebAppCallAPIMethod", {
        method: "groups.getById",
        params: {
          group_id: group_id,
          v: "5.101",
          access_token: user_access_token,
        },
      })
      .then((data) => {
        var response = data.response;
        // Get the 1st element of the "Groups" array
        var group = response[0];
        var group_id = group.id;
        console.log("Group ID: " + group_id);
        this.setState({
          group: group,
        });

        this.getGroupRatingById(group_id)
          .then((response) => {
            // DEBUG
            this.setState({
              debug: JSON.stringify(response),
            });

            var result = response.data;
            this.setState({
              result: result,
            });

            if (!result)
              this.setState({
                error: (
                  <div className="alert alert-danger" role="alert">
                    <p>Сведения о группе {group.name} отсутствуют.</p>
                    Это Ваша группа?{" "}
                    <Link to={process.env.PUBLIC_URL + "/business"}>
                      Установите приложение
                    </Link>{" "}
                    и добавьте виджет.
                  </div>
                ),
              });
            else this.setState({ error: null });

            console.log("getGroupRatingById (axios) Result:");
            console.log(result);

            this.setState({
              loading: false,
            });
          })
          .catch((error) => {
            console.log("getGroupRatingById (axios) Error:");
            console.log(error);
            this.setState({
              loading: false,
            });

            // DEBUG
            this.setState({
              debug: JSON.stringify(error),
            });
          });
      })
      .catch((error) => {
        console.log("Error:");
        console.log(error);
        this.setState({
          error: (
            <div className="alert alert-danger" role="alert">
              Группа {group_id} не найдена. Пожалуйста, проверьте ссылку и
              попробуйте еще раз.
            </div>
          ),
          result: null,
          loading: false,
        });
      });
  }

  render() {
    const { result, error, loading, group } = this.state;

    const form = (
      <form onSubmit={this.onSubmitForm}>
        <div className="form-group">
          <label htmlFor="groupUrl">Ссылка на группу</label>
          <input
            type="text"
            className="form-control form-control-lg"
            id="groupUrl"
            placeholder="Введите ссылку на группу"
            value={this.state.group_url}
            onChange={this.onChangeGroupUrl}
          />
          <small id="groupUrlHelp" className="form-text text-muted">
            Например, vk.com/club100
          </small>
        </div>
        <button
          type="submit"
          className="btn btn-primary btn-lg btn-block"
          disabled={loading}
        >
          {loading ? "Загрузка..." : "Проверить"}
        </button>
        <Link
          to={process.env.PUBLIC_URL + "/"}
          className="btn btn-light btn-lg btn-block"
          role="button"
        >
          Назад
        </Link>
      </form>
    );

    const spinner = loading ? (
      <div className="d-flex justify-content-center">
        <div className="spinner-border text-primary" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    ) : null;

    return (
      <div>
        <h1>Проверить группу</h1>
        <p>
          На этой странице можно найти сведения о конкретной группе. Пожалуйста,
          введите ссылку на группу и нажмите "Проверить".
        </p>
        {loading ? <p>Пожалуйста, подождите...</p> : ""}
        {spinner}
        {result ? (
          <div>
            <div>
              <a
                className="alert-link"
                href={"https://vk.com/club" + result.group_id}
                rel="noopener noreferrer"
                target="_blank"
                title={group.name}
              >
                <img src={group.photo_50} alt={group.name} />
              </a>
              <a
                className="alert-link ml-3"
                href={"https://vk.com/club" + result.group_id}
                rel="noopener noreferrer"
                target="_blank"
              >
                {group.name}
              </a>
            </div>
            <StarRating rating={result.rating} />
            <p>
              <Link to={process.env.PUBLIC_URL + "/feedback"}>
                Все отзывы »
              </Link>
            </p>
          </div>
        ) : null}
        {error}
        {form}
        {/* <p>{this.state.debug}</p> */}
      </div>
    );
  }
}

export default Checker;
