import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import bridge from "@vkontakte/vk-bridge";
import Utils from "./Utils";

class Business extends Component {
  constructor() {
    super();
    this.state = {
      isAppAdded: false,
      isWidgetInstalled: false,
      group: null,
      debug: null,
    };
  }

  componentDidMount() {
    console.log(sessionStorage);
    // Get User AuthToken
    var user_access_token = sessionStorage.getItem("user_access_token");
    var app_id = sessionStorage.getItem("app_id");
    if (!user_access_token || !app_id) {
      // Get User AuthToken From Launch Params
      var queryParams = Utils.parseQueryString(window.location.search);
      console.log("queryParams:");
      console.log(queryParams);

      app_id = queryParams["vk_app_id"];
      this.getAuthToken(app_id);
    }
  }

  getAuthToken = (app_id) => {
    bridge
      .send("VKWebAppGetAuthToken", {
        app_id: Number(app_id),
        scope: "groups",
      })
      .then((data) => {
        var user_access_token = data.access_token;
        console.log("User Access Token: " + user_access_token);
        sessionStorage.setItem("user_access_token", user_access_token);
        sessionStorage.setItem("app_id", app_id);
      })
      .catch((error) => {});
  };

  addToCommunity = () => {
    bridge
      .send("VKWebAppAddToCommunity")
      .then((data) => {
        // Обработка события в случае успеха
        this.setState({
          isAppAdded: true,
        });
        let group_id = data.group_id;
        let app_id = 7266551;
        this.getGroupAccessToken(app_id, group_id);
      })
      .catch((error) => {
        // Обработка события в случае ошибки
      });
  };

  getGroupAccessToken = (app_id, group_id) => {
    bridge
      .send("VKWebAppGetCommunityToken", {
        app_id: Number(app_id),
        group_id: Number(group_id),
        scope: "app_widget",
      })
      .then((data) => {
        console.log("Group Access Token:");
        console.log(data);
        var group_access_token = data.access_token;
        var user_access_token = sessionStorage.getItem("user_access_token");
        console.log("User Access Token: " + user_access_token);

        // Call to VK API
        this.getGroupsById(group_id, user_access_token)
          .then((data) => {
            var response = data.response;
            var group = response[0];

            // Adjust the Parameter Names to the DB
            group.group_id = group.id;
            group.access_token = group_access_token;
            group.status = 1;

            // Use HTTPS!
            // The ngrok Backend URL string is for local Development
            // var backend_url = "https://adc3deeada67.ngrok.io";
            // Heroku Backend
            var backend_url = "https://test-mern-app-2020-06-04.herokuapp.com";
            backend_url += "/";
            axios
              .get(backend_url + "groups/" + group.group_id)
              .then((res) => {
                var data = res.data;
                if (data) {
                  // Update the Access Token
                  axios
                    .post(
                      backend_url + "groups/update/" + group.group_id,
                      group
                    )
                    .then((res) => {
                      console.log("axios – Add a New Group Reponse:");
                      console.log(res.data);
                    })
                    .catch((err) => {
                      console.log("axios – Add a New Group Error:");
                      console.log(res.error);
                    });
                } else {
                  // Insert a new group into the DB
                  axios
                    .post(backend_url + "groups/add", group)
                    .then((res) => {
                      console.log("axios – Update Group Reponse:");
                      console.log(res.data);
                    })
                    .catch((err) => {
                      console.log("axios – Update Group Error:");
                      console.log(res.error);
                    });
                }
                console.log(res.data);
              })
              .catch((err) => {
                console.log("axios – Get Group From the DB by ID Error:");
                console.log(err);
              });

            this.showCommunityWidgetPreviewBox(group);
          })
          .catch((error) => {
            console.log("getGroupsById Error:");
            console.log(error);
          });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  getGroupsById = (group_id, access_token) => {
    return new Promise((resolve, reject) => {
      // Get Group By ID
      bridge
        .send("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          params: {
            group_id: group_id,
            v: "5.103",
            access_token: access_token,
          },
        })
        .then((data) => {
          // Handle Success
          resolve(data);
        })
        .catch((error) => {
          // Handle Error
          reject(error);
        });
    });
  };

  showCommunityWidgetPreviewBox = (group) => {
    var type = "cover_list";
    // Баннер виджета
    var link1 = "#group_id=" + group.id;
    // Кнопка
    var link2 = "#group_id=" + group.id;
    // Общий Title ("Всего в базе")
    var link3 = "";
    var code =
      "return {" +
      // Main Title (Top Left)
      '"title": "Всего в базе",' +
      '"title_url": "https://vk.com/app7266551' +
      link3 +
      '",' +
      '"title_counter": "999",' +
      '"rows": [{' +
      // Row Title (Bottom Left) Required
      '"title": "' +
      group.name +
      '",' +
      // Row Description (до 100 символов)
      '"descr": "ID: ' +
      group.id +
      '",' +
      // Status Negative (здоровы)
      '"cover_id": "7266551_524825",' +
      // Status Checking
      // '"cover_id": "7266551_524826",' +
      // Status No Data
      // '"cover_id": "7266551_524824",' +
      // Ссылка для всего элемента
      '"url": "https://vk.com/app7266551_-' +
      group.id +
      link1 +
      '",' +
      '"button": "Проверить",' +
      '"button_url": "https://vk.com/app7266551_-' +
      group.id +
      link2 +
      '"' +
      "}]" +
      "};";

    bridge
      .send("VKWebAppShowCommunityWidgetPreviewBox", {
        group_id: Number(group.id),
        type: type,
        code: code,
      })
      .then((data) => {
        this.setState({
          isWidgetInstalled: true,
          group: group,
        });
      })
      .catch((error) => {});
  };

  render() {
    const { isWidgetInstalled, group } = this.state;
    return (
      <div>
        {isWidgetInstalled ? (
          <div className="alert alert-success" role="alert">
            <h4 className="alert-heading">Успешно!</h4>
            <p>
              Виджет установлен. Теперь Вы можете перейти в{" "}
              <a
                className="alert-link"
                href={"https://vk.com/club" + group.id}
                rel="noopener noreferrer"
                target="_blank"
              >
                {group.name}
              </a>
              .
            </p>
          </div>
        ) : null}
        <h1>Для групп</h1>
        <p>
          На этой странице Вы можете установить приложение к себе в группу и
          добавить виджет на главную страницу.
        </p>
        <p>
          Если Вы уже установили приложение, но виджет не отображается – удалите
          приложение из группы и добавьте заново с установкой виджета.
        </p>
        <p>
          <button
            onClick={this.addToCommunity}
            type="button"
            className="btn btn-primary btn-lg btn-block"
          >
            Установить
          </button>
          <Link
            to={process.env.PUBLIC_URL + "/"}
            className="btn btn-light btn-lg btn-block"
            role="button"
          >
            Назад
          </Link>
        </p>
      </div>
    );
  }
}

export default Business;
