import React from "react";
import { Link } from "react-router-dom";

function Quarantine() {
  return (
    <React.Fragment>
      <h1>Карантин</h1>
      <p>
        Под термином "на карантине" понимается присвоение конкретному сообществу
        на ограниченный период условного статуса, который служит индикатором для
        потребителей об увеличенном риске передачи вируса через контактное
        взаимодействие с персоналом или через приобретаемые товары.
      </p>
      <p>
        "Карантин" снимается по прошествии 14 дней, если в течение этого периода
        среди персонала группы не было выявлено новых случаев заражения.
      </p>
      <Link
        to={process.env.PUBLIC_URL + "/"}
        className="btn btn-light btn-lg btn-block"
        role="button"
      >
        Назад
      </Link>
    </React.Fragment>
  );
}

export default Quarantine;
